//@ts-check

import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";

function setHeaders() {
	const authorization = 'Basic ' + window.btoa(localStorage.getItem('name') + ':' + localStorage.getItem('token'));
	//const authorization = 'Basic YWRtaW46ZHRkZ1A3blZmRWQ3VHpQbjRkMjFlaU5FdEtOYUc0UXZBdkNJ';
	const headers = {
		'Accept': 'application/json',
		'Content-Type': 'application/json',
		'Authorization': authorization
	}

	return headers;
}

export const getUsers = createAsyncThunk(
	'users/getUsers',
	async function () {
		const response = await fetch('/api/users', {
			method: 'GET',
			credentials: 'omit',
			headers: setHeaders()
		});

		if (response.status === 401) {
			return {
				error: 401
			}
		}

		if (!response.ok) {
			throw new Error('Произошла неизвестная ошибка')
		}

		return response.json();
	}
)

export const createUser = createAsyncThunk(
	'users/createUser',
	async function (body) {
		const response = await fetch('/api/users', {
			method: 'POST',
			headers: setHeaders(),
			body: JSON.stringify(body)
		});

		if (!response.ok) {
			const text = await response.text();
			throw new Error(text);
		}

		const responseObject = await response.json();
		const allowed = body.allowed.map((catalog) => ({ name: catalog, deploy: body.deploy }));

		return {
			token: responseObject.token,
			newUser: {
				id: responseObject.id,
				admin: body.isAdmin,
				name: responseObject.name,
				allowed
			}
		}
	}
)

export const deleteUser = createAsyncThunk(
	'users/deleteUser',
	async function (userId) {
		const response = await fetch(`/api/users/${userId}`, {
			method: 'DELETE',
			headers: setHeaders()
		});

		if (!response.ok) {
			throw new Error('Произошла неизвестная ошибка')
		}
	}
)

export const editUser = createAsyncThunk(
	'users/editUser',
	async function (params) {
		const response = await fetch(`/api/users/${params.userId}`, {
			method: 'PUT',
			headers: setHeaders(),
			body: JSON.stringify(params.body)
		});

		if (!response.ok) {
			const text = await response.text();
			throw new Error(text);
		}
	}
)

export const refreshUserToken = createAsyncThunk(
	'users/refreshToken',
	async function (userId) {
		const response = await fetch(`/api/users/${userId}/refresh`, {
			method: 'GET',
			headers: setHeaders(),
		});

		if (!response.ok) {
			throw new Error('Произошла неизвестная ошибка')
		}

		return response.json();
	}
)

const userSlice = createSlice({
	name: 'users',
	initialState: {
		users: null,
		loading: false,
		hasCredentials: !!(localStorage.getItem('name'))
	},
	reducers: {
		setHasCredentials: (state, action) => {
			state.hasCredentials = action.payload;
		}
	},
	extraReducers: {
		[getUsers.pending]: (state) => {
			state.loading = true;
		},
		[getUsers.fulfilled]: (state, action) => {
			state.users = action.payload.items;
			state.loading = false;
		},
		[getUsers.rejected]: (state) => {
			state.loading = false;
		},
		[createUser.fulfilled]: (state, action) => {
			state.users.push(action.payload.newUser);
		},
		[deleteUser.fulfilled]: (state, action) => {
			state.users = state.users.filter((user) => user.id !== action.meta.arg);
		},
		[editUser.fulfilled]: (state, action) => {
			const userToEdit = state.users.find((user) => user.id === Number(action.meta.arg.userId));
			action.meta.arg.body.allowed.forEach(catalog => userToEdit.allowed.push({ name: catalog, deploy: action.meta.arg.body.deploy }));
		},
	},
});

export const { setHasCredentials } = userSlice.actions;
export default userSlice.reducer;