//@ts-check

import React, { useState } from 'react';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { useDispatch } from "react-redux";
import { useNavigate } from 'react-router-dom';
import { Form, FormGroup, Input, InputGroup, Label } from 'reactstrap';
import { createUser } from '../../../store/userSlice';
import ErrorAlert from '../../ReusableComponents/ErrorAlert/ErrorAlert';
import NegativeButton from '../../ReusableComponents/NegativeButton/NegativeButton';
import PositiveButton from '../../ReusableComponents/PositiveButton/PositiveButton';
import TokenAlert from '../../ReusableComponents/TokenAlert/TokenAlert';
import './UserCreationPage.css';

export default function UserCreation(props) {
	const [userName, setUserName] = useState('');
	const [isDeployAllowed, setIsDeployAllowed] = useState(false);
	const [isAdmin, setIsAdmin] = useState(false);
	const [availableCatalogs, setAvailableCatalogs] = useState(['']);

	const dispatch = useDispatch();

	let navigate = useNavigate();

	const availableCatalogsInputs = availableCatalogs.map((catalog, index) =>
		<InputGroup>
			<Input
				key={index}
				type='text'
				value={catalog}
				onChange={(event) => {
					const newAvailableCatalogs = [...availableCatalogs];
					newAvailableCatalogs[index] = event.target.value;
					setAvailableCatalogs(newAvailableCatalogs)
				}}
			/>
			{index !== 0 && <NegativeButton outline={true} onClick={() => onDeleteCatalogButtonClicked(index)}>
				x
			</NegativeButton>}
		</InputGroup>);

	function onAddCatalogButtonClicked() {
		const newAvailableCatalogs = [...availableCatalogs];
		newAvailableCatalogs.push('');
		setAvailableCatalogs(newAvailableCatalogs);
	}

	function onDeleteCatalogButtonClicked(index) {
		const newAvailableCatalogs = availableCatalogs.filter((catalog, i) => index !== i);
		setAvailableCatalogs(newAvailableCatalogs);
	}

	function onCreateUserFormSubmitted(event) {
		event.preventDefault();
		dispatch(createUser({
			name: userName,
			deploy: isDeployAllowed,
			admin: isAdmin,
			allowed: availableCatalogs.filter(catalog => catalog !== '')
		}))
			.unwrap()
			.then((result) => {
				confirmAlert({
					customUI: ({ onClose }) => {
						return (
							<TokenAlert
								title={'Создан новый пользователь с именем ' + userName}
								token={result.token}
								onConfirm={() => {
									onClose();
									navigate("/");
								}} />
						);
					},
					onClickOutside: () => { navigate("/"); },
				});

				setUserName('');
				setIsDeployAllowed(false);
				setIsAdmin(false);
				setAvailableCatalogs(['']);
			})
			.catch((err) => {
				confirmAlert({
					customUI: ({ onClose }) => {
						return (
							<ErrorAlert
								title={'При создании пользователя возникла следующая ошибка:'}
								errorMessage={err.message}
								onClose={onClose} />
						);
					},
				});
			});
	}

	return (
		<div>
			<h1 className='creation-page-title'>Создание нового пользователя</h1>
			<Form onSubmit={onCreateUserFormSubmitted}>
				<FormGroup>
					<Label for='name'>
						Имя пользователя
					</Label>
					<Input
						id='name'
						type='text'
						value={userName}
						onChange={(event) => setUserName(event.target.value)}
					/>
				</FormGroup>
				<FormGroup>
					<Label>
						Введите имя доступного каталога для пользователя
					</Label>
					{availableCatalogsInputs}
				</FormGroup>

				<PositiveButton
					className='add-catalog-button'
					onClick={onAddCatalogButtonClicked}
					outline={true}>Добавить еще один каталог</PositiveButton>

				<FormGroup>
					<Input
						id='is-deploy-allowed'
						type='checkbox'
						checked={isDeployAllowed}
						onChange={(event) => setIsDeployAllowed(event.target.checked)}
					/>{' '}
					<Label for='is-deploy-allowed'>
						Можно разворачивать артефакты
					</Label>
				</FormGroup>
				<FormGroup>
					<Input
						id='is-admin'
						type='checkbox'
						checked={isAdmin}
						onChange={(event) => setIsAdmin(event.target.checked)}
					/>{' '}
					<Label for='is-admin'>
						Админ
					</Label>
				</FormGroup>
				<PositiveButton buttonType='submit' disabled={userName === '' || availableCatalogs.filter((catalog) => catalog !== '').length === 0}>Создать</PositiveButton>
			</Form>
		</div>
	)
};