//@ts-check

import React from 'react';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { Button } from 'reactstrap';
import NegativeButton from '../NegativeButton/NegativeButton';
import './AreYouSureConfirm.css';

export default function AreYouSureConfirm(props) {
	return (
		<div className='are-you-sure-confirm'>
			<h2 className='modal-title'>{props.title}</h2>
			<div className='modal-question'>{props.question}</div>
			<NegativeButton className='confirm-button'
				onClick={props.onConfirm}>Да</NegativeButton>
			<Button onClick={props.onClose}>Нет</Button>
		</div>
	);
}