//@ts-check

import React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { setHasCredentials } from '../../store/userSlice';
import AreYouSureConfirm from '../ReusableComponents/AreYouSureConfirm/AreYouSureConfirm';
import NegativeButton from '../ReusableComponents/NegativeButton/NegativeButton';

export default function LogoutHeader() {
	const dispatch = useDispatch();
	const navigate = useNavigate();

	function onLogoutButtonClicked() {
		confirmAlert({
			customUI: ({ onClose }) => {
				return (
					<AreYouSureConfirm
						title={'Выход'}
						question={'Вы действительно хотите выйти?'}
						onClose={onClose}
						onConfirm={() => {
							onClose();
							dispatch(setHasCredentials(false));
							localStorage.removeItem('name');
							localStorage.removeItem('token');
							navigate('/login');
						}} />
				);
			}
		});
	}
	return (
		<NegativeButton onClick={onLogoutButtonClicked} outline={true}>Выйти</NegativeButton>
	)
}