//@ts-check

import React from 'react';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { toast } from 'react-toastify';
import { Alert } from 'reactstrap';
import PositiveButton from '../../ReusableComponents/PositiveButton/PositiveButton';
import './TokenAlert.css';


export default function TokenAlert(props) {
	return (
		<div className='token-alert'>
			<h2 className='modal-title'>{props.title}</h2>
			<Alert color="warning">Токен показывается один раз, повторно его посмотреть нельзя.</Alert>
			<div>Токен пользователя:</div>
			<input size={40} value={props.token} />
			<CopyToClipboard text={props.token} onCopy={() => toast.success("Токен скопирован")}>
				<button className='copy-button'></button>
			</CopyToClipboard>
			<PositiveButton className='confirm-button'
				onClick={props.onConfirm}>
				ОК</PositiveButton>
		</div>
	);
}