//@ts-check

import React, { useState } from 'react';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import { toast } from 'react-toastify';
import { Alert, Form, FormGroup, Input, InputGroup, Label } from 'reactstrap';
import { editUser } from '../../../store/userSlice';
import ErrorAlert from '../../ReusableComponents/ErrorAlert/ErrorAlert';
import Loader from '../../ReusableComponents/Loader/Loader';
import NegativeButton from '../../ReusableComponents/NegativeButton/NegativeButton';
import PositiveButton from '../../ReusableComponents/PositiveButton/PositiveButton';
import './UserEditionPage.css';

export default function UserEdition(props) {
	const { userId } = useParams();
	const users = useSelector((state) => state.users.users);

	const [isDeployAllowed, setIsDeployAllowed] = useState(false);
	const [addedAvailableCatalogs, setAddedAvailableCatalogs] = useState(['']);

	let navigate = useNavigate();

	const dispatch = useDispatch();

	if (users == null) {
		return (
			<Loader>Загрузка...</Loader>
		)
	}

	const userToEdit = users.find((user) => Number(userId) === user.id);

	const availableCatalogsInputs = addedAvailableCatalogs.map((catalog, index) =>
		<InputGroup>
			<Input
				key={index}
				type='text'
				value={catalog}
				onChange={(event) => {
					const newAddedAvailableCatalogs = [...addedAvailableCatalogs];
					newAddedAvailableCatalogs[index] = event.target.value;
					setAddedAvailableCatalogs(newAddedAvailableCatalogs)
				}}
			/>
			{index !== 0 && <NegativeButton outline={true} onClick={() => onDeleteCatalogButtonClicked(index)}>
				x
			</NegativeButton>}
		</InputGroup>
	);

	function onAddCatalogButtonClicked() {
		const newAddedAvailableCatalogs = [...addedAvailableCatalogs];
		newAddedAvailableCatalogs.push('');
		setAddedAvailableCatalogs(newAddedAvailableCatalogs);
	}

	function onDeleteCatalogButtonClicked(index) {
		const newAddedAvailableCatalogs = addedAvailableCatalogs.filter((catalog, i) => index !== i);
		setAddedAvailableCatalogs(newAddedAvailableCatalogs);
	}

	function onEditUserFormSubmitted(event) {
		event.preventDefault();
		dispatch(editUser({
			userId,
			body: {
				deploy: isDeployAllowed,
				allowed: addedAvailableCatalogs.filter(catalog => catalog !== '')
			}
		}))
			.unwrap()
			.then(() => {
				toast.success("Пользователь отредактирован");
				navigate("/");
			})
			.catch((err) => {
				confirmAlert({
					customUI: ({ onClose }) => {
						return (
							<ErrorAlert
								title={'При редактировании пользователя возникла следующая ошибка:'}
								errorMessage={err.message}
								onClose={onClose} />
						);
					},
				});
			})

		setIsDeployAllowed(false);
		setAddedAvailableCatalogs(['']);
	}

	const availableCatalogNames = userToEdit.allowed.map((availableCatalog) => <li key={availableCatalog.name} className='catalog-name'>{availableCatalog.name}</li>);

	return (
		<div className='user-creation'>
			<h1 className='edition-page-title'>Редактирование пользователя</h1>

			<div className='user-name'><span className='user-name-title'>Имя пользователя: </span>{userToEdit.name}</div>

			<div className='available-catalog-names-title'>Доступные каталоги:</div>
			<ul className='edition-page-catalog-list'>{availableCatalogNames}</ul>

			<Alert color="warning">
				На текущий момент сервис GoMVN предоставляет возможность только добавить каталоги.
			</Alert>

			<Form onSubmit={onEditUserFormSubmitted}>
				<FormGroup>
					<Label className='edition-page-add-catalog'>
						Добавьте имя доступного каталога для пользователя
					</Label>
					{availableCatalogsInputs}
				</FormGroup>

				<PositiveButton
					className='add-catalog-button'
					onClick={onAddCatalogButtonClicked}
					outline={true}>Добавить еще один каталог</PositiveButton>

				<FormGroup>
					<Input
						id='is-deploy-allowed'
						type='checkbox'
						checked={isDeployAllowed}
						onChange={(event) => setIsDeployAllowed(event.target.checked)}
					/>{' '}
					<Label for='is-deploy-allowed'>
						Можно разворачивать артефакты
					</Label>
				</FormGroup>
				<PositiveButton buttonType='submit' disabled={addedAvailableCatalogs.filter((catalog) => catalog !== '').length === 0}>Сохранить изменения</PositiveButton>
			</Form>
		</div>
	)
};