//@ts-check

import 'bootstrap/dist/css/bootstrap.min.css';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Route, Routes, useNavigate } from 'react-router-dom';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Container, Spinner } from 'reactstrap';
import Loader from '../ReusableComponents/Loader/Loader';
import { getUsers } from '../../store/userSlice';
import Logout from '../Logout/Logout';
import LoginPage from '../Pages/LoginPage/LoginPage';
import UserCreationPage from '../Pages/UserCreationPage/UserCreationPage';
import UserEditionPage from '../Pages/UserEditionPage/UserEditionPage';
import UserListPage from '../Pages/UserListPage/UserListPage';
import './App.css';

function App() {
	const dispatch = useDispatch();
	const navigate = useNavigate();

	useEffect(() => {
		dispatch(getUsers())
			.unwrap()
			.then((result) => {
				if (result.error === 401) {
					navigate("/login");
				}
			})
			.catch((err) => {
				toast.error(err.message);
			})
	}, []);

	const users = useSelector((state) => state.users.users);
	const loading = useSelector((state) => state.users.loading);
	const hasCredentials = useSelector((state) => state.users.hasCredentials);

	if (!users && loading) {
		return (
			<Loader>Загрузка...</Loader>
		);
	}

	return (
		<Container>
			<ToastContainer />
			{hasCredentials &&
				<header className='page-header'>
					<Logout />
				</header>}
			<Routes>
				<Route path="/" element={<UserListPage />} />
				<Route path="/edit-user/:userId" element={<UserEditionPage />} />
				<Route path="/create-user" element={<UserCreationPage />} />
				<Route path="/login" element={<LoginPage />} />
			</Routes>
		</Container>
	);
}

export default App;