//@ts-check
import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import { Form, FormGroup, Input, Label } from 'reactstrap';
import { getUsers, setHasCredentials } from '../../../store/userSlice';
import PositiveButton from '../../ReusableComponents/PositiveButton/PositiveButton';
import './LoginPage.css';

export default function LoginPage() {
	const [userName, setUserName] = useState('');
	const [userToken, setUserToken] = useState('');

	const navigate = useNavigate();
	const dispatch = useDispatch();

	function onLoginFormSubmitted(event) {
		event.preventDefault();
		localStorage.setItem('name', userName);
		localStorage.setItem('token', userToken);

		dispatch(getUsers())
			.unwrap()
			.then((result) => {
				if (result.error === 401) {
					toast.error('Некорректные учетные данные');
					setUserName('');
					setUserToken('');
					localStorage.removeItem('name');
					localStorage.removeItem('token');
				} else {
					toast.success('Вход успешно осуществлен');
					dispatch(setHasCredentials(true));
					navigate("/");
				}

			})
			.catch((err) => {
				toast.error(err.message);
			})
	}

	return (
		<div className='login-page'>
			<h1>Вход</h1>
			<Form onSubmit={onLoginFormSubmitted}>
				<FormGroup>
					<Label for='name'>
						Имя
					</Label>
					<Input
						id='name'
						type='text'
						value={userName}
						onChange={(event) => setUserName(event.target.value)}
					/>
				</FormGroup>
				<FormGroup>
					<Label for='token'>
						Токен
					</Label>
					<Input
						id='token'
						type='text'
						value={userToken}
						onChange={(event) => setUserToken(event.target.value)}
					/>
				</FormGroup>
				<PositiveButton buttonType='submit'>Войти</PositiveButton>
			</Form>
		</div>
	)
}