//@ts-check

import React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { useDispatch } from "react-redux";
import { Link, useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import { Card, CardBody, CardText, CardTitle } from 'reactstrap';
import editPic from '../../../../images/edit-color.png';
import { deleteUser, refreshUserToken } from '../../../../store/userSlice';
import AreYouSureConfirm from '../../../ReusableComponents/AreYouSureConfirm/AreYouSureConfirm';
import TokenAlert from '../../../ReusableComponents/TokenAlert/TokenAlert';
import './User.css';

export default function User(props) {
	const dispatch = useDispatch();

	function onDeleteUserButtonClicked() {
		confirmAlert({
			customUI: ({ onClose }) => {
				return (
					<AreYouSureConfirm
						title={'Удаление пользователя'}
						question={'Вы действительно хотите удалить пользователя ' + props.user.name + '?'}
						onClose={onClose}
						onConfirm={() => {
							onClose();
							dispatch(deleteUser(props.user.id))
								.unwrap()
								.then(() => {
									toast.success(`Пользователь ${props.user.name} удален`);
								})
								.catch((err) => {
									toast.error(err.message);
								})
						}} />
				);
			}
		});
	}

	let navigate = useNavigate();

	function onRefreshTokenButtonClicked() {
		confirmAlert({
			customUI: ({ onClose }) => {
				return (
					<AreYouSureConfirm
						title={'Сброс токена пользователя'}
						question={'Вы действительно хотите сбросить токен пользователя ' + props.user.name + '?'}
						onClose={onClose}
						onConfirm={() => {
							onClose();
							dispatch(refreshUserToken(props.user.id))
								.unwrap()
								.then((result) => {
									confirmAlert({
										customUI: ({ onClose }) => {
											return (
												<TokenAlert
													title={'Сгенерирован новый токен для пользователя с именем ' + props.user.name}
													token={result.token}
													onConfirm={() => {
														onClose();
														navigate("/");
													}} />
											);
										},
										onClickOutside: () => { navigate("/"); },
									});
								})
								.catch((err) => {
									toast.error(err.message);
								})
						}} />
				);
			}
		});
	}

	const availableCatalogNames = props.user.allowed.map((availableCatalog) => <li key={availableCatalog.name} className={'catalog-name ' + (availableCatalog.deploy ? 'deploy' : '')} >{availableCatalog.name}</li>);

	return (
		<li className="user">
			<Card className='user-card'>
				<CardBody>
					<CardTitle tag="h5">
						{props.user.name}
					</CardTitle>
					<CardText tag="div">
						<div>Доступные каталоги:</div>
						<ul className='catalog-list'>{availableCatalogNames}</ul>
					</CardText>
				</CardBody>
				<Link className='edit-link' to={'/edit-user/' + props.user.id}><img className='edit-link-img' alt='edit-link' src={editPic} width='20px' height='20px' /></Link>
				<button className='refresh-token-button' onClick={onRefreshTokenButtonClicked} type="button"></button>
				<button className='delete-button' onClick={onDeleteUserButtonClicked} type="button"></button>
			</Card>
		</li>
	)
};